body {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

.header {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-wrapper {
  align-items: center;
  display: flex;
}

.search-input {
  border-radius: 10px;
  margin: 0 10px 25px;
  padding: 2px 5px;
  font-size: 1em;
}

button {
  background-color: #b2b3c3;
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 1em;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, .4);
}

button:hover {
  background-color: #5e5f67;
}

button:active {
  box-shadow: inset 5px 5px 10px rgba(0, 0, 0, .6);
}

.search-btn {
  margin-bottom: 25px;
}

.load-more {
  margin: 20px auto;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: calc(50% - 20px);
  box-sizing: border-box;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, .3);
}

img {
  display: block;
}

.photo-card img {
  width: 100%;
  height: 75%;
  object-fit: cover;
  border-radius: 20px;
  transition: transform .3s ease-in-out;
  overflow: hidden;
}

.photo-card:hover img {
  transform: scale(1.05);
}

.info {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 5px;
  display: flex;
}

.info p {
  margin: 0;
  font-size: 6px;
}

@media screen and (max-width: 767px) {
  .info p {
    width: 50%;
  }
}

@media screen and (min-width: 768px) {
  .photo-card {
    width: calc(33% - 20px);
  }

  .search-input {
    border-radius: 20px;
    margin: 0 10px 25px;
    padding: 5px 10px;
    font-size: 1.3em;
  }

  button {
    border-radius: 20px;
    padding: 5px 10px;
    font-size: 1.3em;
  }

  .info {
    margin: 10px 5px 0;
  }

  .info p {
    font-size: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .photo-card {
    width: calc(25% - 20px);
  }

  .info {
    margin: 20px 5px 0;
  }

  .info p {
    font-size: 14px;
  }
}

@media screen and (min-width: 1600px) {
  .photo-card {
    width: calc(20% - 20px);
  }
}

