body {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.header {
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Wyśrodkowanie elementów w pionie */
}

.search-wrapper {
    display: flex;
    align-items: center;
    /* Wyśrodkowanie elementów w pionie */
}

.search-input {
    margin: 0px 10px 25px;
    padding: 2px 5px;
    font-size: 1em;
    border-radius: 10px;
}

button {
    font-size: 1em;
    padding: 5px 10px;
    border: none;
    border-radius: 10px;
    background-color: #b2b3c3;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

button:hover {
    background-color: #5e5f67;
}

button:active {
    box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.6);
}

.search-btn {
    margin-bottom: 25px;
}


.load-more {
    margin: 20px auto;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    /* Przerwa między zdjęciami */
}

.photo-card {
    width: calc(50% - 20px);
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    border-radius: 2px;
    border: 1px solid #ccc;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.photo-card:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

img {
    display: block;
}

.photo-card img {
    width: 100%;
    height: 75%;
    object-fit: cover;
    overflow: hidden;
    border-radius: 20px;
    transition: transform 0.3s ease-in-out;
}

.photo-card:hover img {
    transform: scale(1.05);
    /* Powiększenie zdjęcia po najechaniu */
}

.info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 0px 5px 0px 5px;
}

.info p {
    margin: 0;
    font-size: 6px;
    /* Rozmiar czcionki dla mobile */
}

@media screen and (max-width: 767px) {
    .info p {
        width: 50%;
        /* Dwie kolumny dla urządzeń mobilnych */
    }
}

@media screen and (min-width: 768px) {
    .photo-card {
        width: calc(33% - 20px);
        /* 33% szerokości - marginesy */
    }

    .search-input {
        margin: 0px 10px 25px;
        padding: 5px 10px;
        font-size: 1.3em;
        border-radius: 20px;
    }

    button {
        font-size: 1.3em;
        padding: 5px 10px;
        border-radius: 20px;
    }

    .info {
        margin: 10px 5px 0px 5px;
    }

    .info p {
        font-size: 10px;
        /* Zwiększenie rozmiaru czcionki dla tabletów */
    }
}

@media screen and (min-width: 1200px) {
    .photo-card {
        width: calc(25% - 20px);
        /* 25% szerokości - marginesy */
    }

    .info {
        margin: 20px 5px 0px 5px;
    }

    .info p {
        font-size: 14px;
        /* Zwiększenie rozmiaru czcionki dla tabletów */
    }
}

@media screen and (min-width: 1600px) {
    .photo-card {
        width: calc(20% - 20px);
        /* 20% szerokości - marginesy */
    }
}